import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmSettings,
  VmMenu 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-settings', VmSettings);
define('vm-menu', VmMenu);
  

export interface SettingsProps extends JSX.VmSettings, HTMLAttributes<HTMLVmSettingsElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmSettingsElement, SettingsProps>(
  'vm-settings',
  new Set(['pin','active','isMobile','isAudioView']),
);
  